/* Overlay when sidebar is open */
.cart-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.35);
  z-index: 150;
  cursor: pointer;
  transition: opacity 0.3s ease;
}

/* Sidebar container */
.cart-sider {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 350px;
  max-width: 90vw;
  background-color: #fff;
  box-shadow: -4px 0 12px rgba(0, 0, 0, 0.12);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 200;
  display: flex;
  flex-direction: column;
  padding: 24px;
  box-sizing: border-box;
  overflow: hidden;
}

/* Open state for sidebar */
.cart-sider.open {
  transform: translateX(0);
}

/* Header styles */
.cartSider-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ddd;
  padding-bottom: 12px;
  margin-bottom: 20px;
  user-select: none;
}

.cartSider-header h3 {
  font-size: 1.6rem;
  font-weight: 700;
  color: #222;
  margin: 0;
}

.cartSider-header h3 span {
  font-weight: 400;
  color: #555;
  margin-left: 8px;
}

/* Close button */
.close-button {
  background: none;
  border: none;
  font-size: 1.8rem;
  cursor: pointer;
  color: #777;
  transition: color 0.2s ease;
  padding: 4px;
  line-height: 1;
}

.close-button:hover {
  color: #000;
}

/* Content container */
.cart-content {
  flex-grow: 1;
  overflow-y: auto;
  padding-right: 8px;
}

/* Item list wrapper */
.itemList {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

/* Footer section */
.cart-footer {
  border-top: 1px solid #ddd;
  padding-top: 16px;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

/* Clear Cart button */
.clear-cart-btn {
  background-color: #ff4136;
  color: white;
  border: none;
  padding: 12px 0;
  font-size: 1rem;
  font-weight: 700;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.clear-cart-btn:hover {
  background-color: #e03e2f;
}

/* Cart total */
.cart-total p {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 600;
  color: #111;
}

.cart-total p span {
  font-weight: 400;
  color: #555;
  margin-right: 8px;
}

/* Proceed to Checkout button */
.cart-checkout {
  background-color: #0070f3;
  color: white;
  border: none;
  padding: 14px 0;
  font-size: 1.1rem;
  font-weight: 700;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cart-checkout:hover {
  background-color: #005bb5;
}

/* Scrollbar styling for content */
.cart-content::-webkit-scrollbar {
  width: 8px;
}

.cart-content::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 4px;
}

.cart-content::-webkit-scrollbar-thumb:hover {
  background-color: #aaa;
}
